body {
  --color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  height: 100%;
  margin-right: 20px;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_link {
  cursor: pointer;
  color: #000;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  padding-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 13px;
  text-decoration: none;
  display: flex;
}

.is-active, .nav_link:hover {
  color: var(--color);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
